  font-weight: 400;
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/PTmono-regular.woff2") format("woff2"), url("../fonts/PTmono-regular.woff") format("woff"); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  height: 100%; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
  color: #ffffff;
  background-image: url("../img/bg-promo.jpg");
  height: 100vh; }

.page button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  color: #ffffff;
  letter-spacing: 5%; }

.wrapper {
  max-width: 1440px;
  min-width: 320px;
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }
  @media (max-width: 1023px) {
    .wrapper {
      padding-left: 20px;
      padding-right: 20px;
      margin: 0; } }

.header__wrapper {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 33px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  background-color: transparent; }

.header__button {
  width: 180px;
  height: 45px;
  background-color: transparent;
  color: #ffffff;
  text-transform: uppercase;
  margin-left: 34px;
  margin-top: 5px;
  outline: none;
  border: 1px solid #ffffff;
  border-radius: 5px; }
  .header__button:hover, .header__buttonfocus {
    color: #00b2ff;
    border-color: #00b2ff;
    cursor: pointer; }
  .header__button:active {
    opacity: 30%; }
  @media (max-width: 1023px) {
    .header__button {
      display: none; } }

.navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-left: auto; }

.navigation ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

.navigation li {
  padding-right: 15px; }

.navigation a {
  text-decoration: none;
  outline: none;
  color: #ffffff;
  line-height: 17px;
  padding-left: 75px; }
  .navigation a:hover, .navigation a:focus {
    color: #00b2ff; }
  @media (max-width: 1023px) {
